* {
  margin: 0px;
  padding: 0px;
}

body {
  background-color: black;
}

nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: #0000aa;
}

a {
  text-decoration: none;
  color: white;
}

.logo {
  padding: 15px;
  flex: 2;
  color: white;
  font-size: 19px;
  font-style: italic;
  font-weight: 600;
}

.navigation-elements {
  width: 150px;
  align-self: flex-end;
  display: flex;
  padding: 15px;
  justify-content: space-evenly;
}

.message {
  background-color: black;
  color: orange;
  padding: 10px;
  font-size: small;
  display: flex;
}

.status {
  width: 10px;
  height: 10px;
  background-color: lightgreen;
  display: block;
  border-radius: 10px;
  align-self: center;
  margin-right: 10px;
}

.stop {
  display: flex;
  margin-bottom: 10px;
  border-top: 0.5px rgba(128, 128, 128, 0.295) solid;
}

.stop-img {
  width: 90px;
  height: 90px;
  background-color: grey;
}

.stop-info {
  width: 100%;
}

.stop-id-distance {
  display: flex;
  justify-content: space-between;
}

.stop-id-distance * {
  font-size: 14px;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  color: grey;
}

.stop-name {
  padding-left: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

.stop-routes {
  padding-top: 10px;
  padding-left: 10px;
}

.route {
  width: 20px;
  height: 20px;
  background-color: rgba(197, 196, 196, 0.596);
  padding: 3px;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sechdule-image {
  width: 100%;
  height: 100px;
  background-color: grey;
}

.sechdule-info {
  background-color: rgb(58, 58, 58);
  color: white;
  padding: 10px;
}

.sechdule-info :nth-child(2){
  font-size: smaller;
  color: rgba(248, 248, 248, 0.651);
}

.route-sechdule {
  font-family: monospace;
  font-size: 18px;
  color: white;
  padding: 10px;
  display: flex;
}

.route-sechdule :nth-child(1) {
  flex: 1;
}

.route-sechdule :nth-child(2) {
  flex: 3;
}

.route-sechdule :nth-child(3) {
  flex: 1;
}

.route-sechdule :nth-child(4) {
    flex: 0.5;
}

.schedules {
  position: relative;
  background-color: black;
}